<template>
	<div class="edit">
		<el-form ref="form" :model="editData" label-width="80px" :disabled="true">
	        <el-form-item label="日期">
	          <el-input v-model="editData.date"></el-input>
	        </el-form-item>
					 <el-form-item label="名称">
	          <el-input v-model="editData.name"></el-input>
	        </el-form-item>
					 <el-form-item label="地址">
	          <el-input v-model="editData.address"></el-input>
	        </el-form-item>
	    </el-form>

			<div class="menu">
				<el-button type="primary" @click="saveData">保存</el-button>
				<el-button type="primary" @click="cancelData">取消</el-button>
			</div>
			
	</div>
</template>

<script>
	export default({
		data(){
			return{
				editData: {}
			}
		},
		props: ['editData'],
		methods:{
			saveData() {
				this.$emit('editAdd', null);
			},
			cancelData() {
				this.$emit('cancelEdit', null);
			}
		}
	})
</script>

<style>
.menu {
	text-align: center;
}
</style>


