<template>
	<div class="detail">
		<el-form ref="form" :model="form" label-width="80px">
	        <el-form-item label="日期">
	          <el-input v-model="form.date"></el-input>
	        </el-form-item>
					 <el-form-item label="名称">
	          <el-input v-model="form.name"></el-input>
	        </el-form-item>
					 <el-form-item label="地址">
	          <el-input v-model="form.address"></el-input>
	        </el-form-item>
	    </el-form>

			<div class="operation">
				<el-button type="primary" @click="saveData">保存</el-button>
				<el-button type="danger" @click="cancelData">取消</el-button>
			</div>
	</div>
</template>

<script>
	export default({
		data(){
			return{
				 form: {
		          name: '',
		          address: '',
		          date: ''
		        }
			}
		},
		methods:{
			saveData() {
				if (this.form.name && this.form.date && this.form.address) {
						var a = {"name":this.form.name, "date":this.form.date, "address":this.form.address};
						this.$emit("addDataReload", a);
				}
			},
			cancelData() {
					this.$emit("addDataReload", null);
			}
		}
	})
</script>

<style>
.operation {
	text-align: center;
}
</style>


