<template>
	<div class="list">
		<div class="head">
			<el-input class="searchinput" placeholder="请输入" icon="search" v-model="input2"
		      :on-icon-click="handleIconClick">
			</el-input>	
			<!--Toast提示框-->
			<el-button :plain="true" v-show="false"></el-button>
			<el-button-group class="btns">
			    <el-button type="primary" @click="addClick">新增</el-button>
			    <el-button type="primary" @click="deleteData">删除</el-button>
			    <el-button type="primary" @click="editData">编辑</el-button>
			</el-button-group>
		</div>
		<div class="wrapper">
		
		  <!--列表-->
			<div class="table" v-if="isShowTable"> 
		      <el-table class="tables" 
			    ref="multipleTable"
			    :data="tableData3"
			    border
			    tooltip-effect="dark"
			    style="width: 100%"
			    @selection-change="handleSelectionChange"
					@select = "mySelect"
					>
			    <el-table-column
			      type="selection"
			      width="55">
			    </el-table-column>
			    <el-table-column
			      label="日期"
			      width="120">
			      <template scope="scope">{{ scope.row.date }}</template>
			    </el-table-column>
			    <el-table-column
			      prop="name"
			      label="姓名"
			      width="120">
			    </el-table-column>
			    <el-table-column
			      prop="address"
			      label="地址"
			      show-overflow-tooltip>
			    </el-table-column>
			  </el-table>
		  </div>
			<!--详情页-->
			<detail class="detail" v-else-if="isShowDetail" v-on:addDataReload="addDataReload"></detail>
			<!--编辑页-->
			<edit class="edit" v-else-if="isShowEdit" v-bind:editData="editObj" v-on:cancelEdit="cancelEdit" v-on:editAdd="editAdd"></edit>
	   </div>
	</div>
</template>

<script>
	import detail from "./bossOrgDetail.vue"
	import edit from "./bossOrgEdit.vue"
	export default({
	created() {
		 this.$http.get('static/data.json').then(response => {
			 console.log(response.data);
            this.tableData3 = response.data.data;  
       }, response => {         
      })
	},
  data() {
			return {
			 isShowTable:true,
				isShowEdit:false,
			isShowDetail:false,
      			input2: '',
				tableData3: [],
 multipleSelection: [],
					 editObj:{},
					 originArr:[],
					 oriObj:{},
					 originIndex:0
			}
		},
		components:{
			detail,
			edit
		},
		methods:{
			 handleIconClick(ev) {
		      
		    },
		    toggleSelection(rows) {
		        if (rows) {
		          rows.forEach(row => {
		            this.$refs.multipleTable.toggleRowSelection(row);
		          });
		        } else {
		          this.$refs.multipleTable.clearSelection();
		        }
		    },
		    handleSelectionChange(val) {
		      this.multipleSelection = val;	
		    },
				mySelect(selection, row) {

				},
				editAdd() {
						this.isShowTable = !this.isShowTable;
						this.isShowDetail = false;
						this.multipleSelection.splice(0, 1);
				},
		    onSubmit() {
		       
		    },
				copyArr(arr) {
						let res = []
						for (let i = 0; i < arr.length; i++) {
						    res.push(arr[i])
						}
						return res
				},
				editData() {
					if(this.multipleSelection.length != 1) {
							this.$message('请选择一条数据进行编辑');
							return;
					}

					this.oriObj.date = this.multipleSelection[0].date;
					this.oriObj.name = this.multipleSelection[0].name;
					this.oriObj.address = this.multipleSelection[0].address;

					this.isShowTable = !this.isShowTable;
					this.isShowEdit = true;

					this.multipleSelection.forEach((item, index) => {
							this.tableData3.forEach((item1, index1) => {
										if (item.name == item1.name) {
											this.editObj = this.tableData3[index1];
											this.originIndex = index1;
										}
							});
					});
				},
				deleteData() {
					if (this.multipleSelection.length == 0) {
							this.$message('请选择一条或多条数据进行删除');
							return;
					}
					this.multipleSelection.forEach((item, index) => {
							this.tableData3.forEach((item1, index1) => {
										if (item.name == item1.name) {
											this.tableData3.splice(index1, 1);
										}
							});
					});
				},
				cancelEdit() {
						this.isShowTable = !this.isShowTable;
						this.tableData3.splice(this.originIndex, 1);
						this.tableData3.splice(this.originIndex, 0, this.oriObj);
				},
				addClick(){
					this.isShowTable = !this.isShowTable;
					this.isShowDetail = true;
				},
				seeClick() {
						if (this.multipleSelection.length != 1) {
								this.$message('请选择一条数据进行查看');
						}
				},
				addDataReload(e) {
						this.isShowTable = !this.isShowTable;
						this.isShowDetail = false;
						// 如果e对象不存在，证明是取消操作
					if(e) {
						this.tableData3.push(e);
					}
				}
		}
	})
</script>

<style>
	.list .head{
		background-color: #fff;
	}
	.head{
		position: relative;
	}
	.head .btns{
		position: absolute;
		right: 20px;
		top:10px
	}
	.searchinput {
	  width: 260px;
	  position: absolute;
	  left: 20px;
	  top: 10px;
	}
</style>


